export default {
	data() {
		return {
			receiver:null,
			supplierList:[
					{
						id:1,
						supplierName:'旗舰店',
						supplierAddress:'北湖南路23号',
						checked:true,
						totalAmount:0.98,
						totalNum:3,
						postFee:5,
						careItemList:[
							{
								goodsId:1,
								productId:1,
								isMarketable:false,
								pName:'荔枝',
								price:0.88,
								goodImg:'../../static/logo.png',//listImgPath
								checked:true,
								quantity:2
							}
							
						]
					},
					{
						id:1,
						supplierName:'旗舰店',
						supplierAddress:'北湖南路23号',
						checked:true,
						totalAmount:0.98,
						totalNum:3,
						postFee:5,
						careItemList:[
							{
								goodsId:1,
								productId:1,
								isMarketable:false,
								pName:'荔枝',
								price:0.88,
								goodImg:'../../static/logo.png',//listImgPath
								checked:true,
								quantity:2
							}
							
						]
					},
				],
				selectAllChecked:false,
				totalMoney:0.88,
				totalDistcont:0.88,
				buyType:'',
				pid:''
		}
	},
	onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
		console.log(option.pids); //打印出上个页面传递的参数。
		console.log(option.buyType); //打印出上个页面传递的参数。
		this.buyType = option.buyType;
		this.pids = option.pids;
		
	},
	onShow:function(){
		var data ={buyType:this.buyType,pids:this.pids};
		this.getPreOrderList(data);
	},
	
	methods: {
		
		//跳转支付页，传入传教订单相关参数
		toCreateOrder(){
			if(this.receiver==null){
				uni.showToast({
					title:'请选择收货地址'
				})
				return;
			}
			
			var data = [];
			this.supplierList.forEach((p) => {
				var pids='';
				var quantitys='';
				p.careItemList.forEach((k) => {
					pids+=k.productId+'@@@';
					quantitys+=k.quantity+'@@@'
				});
				var order ={
					sid:p.id,
					pids:pids,
					quantitys:quantitys,
					memo:p.memo
				};
				data.push(order);
			});
		
			var obj = {
				dataJson:data,
				receivierId:this.receiver.id
			}
			let objJson =JSON.stringify(obj);
			uni.redirectTo({
				url: '/pages/pay/pay?objJson='+objJson+'&formorderconfig=formorderconfig'
			});
		},
		
		
		//跳转店铺详情页
		toShopDetail(shopId) {
			uni.navigateTo({
				url: '/pages/shopdetail/shopdetail?shopId='+shopId
			})
		},
		//选择地址
		toSelectAddress(){
			var userId = 1;
			uni.navigateTo({
				url: '/pages/addresslist/addresslist?userId='+userId
			})
		},
		//获取购物车商品
		getPreOrderList(data){
			this.$api.preOrder(data).then(res =>
				{
					 console.log(JSON.stringify(res));
					  console.log(this.$config.imghosturl);
					if(res.list!=null){
						 this.supplierList = res.list;
						 this.totalMoney = res.totalMoney;
						 this.totalDistcont = res.totalDistcont;
						 this.receiver = res.receiver;
					}
				}); 
		},
		
	}
}
